import { MESSAGE_CHANGE, MESSAGE_SAVE_REQUEST, MESSAGE_SAVE_SUCCESS, MESSAGE_SAVE_FAILURE, MESSAGES_SAVE_MESSAGE, MESSAGES_MESSAGE_STATUS_CHANGE, MESSAGE_EDIT, MESSAGES_FETCH_REQUEST, MESSAGES_FETCH_SUCCESS, MESSAGES_FETCH_FAILURE, MESSAGES_DELETE_REQUEST, MESSAGES_DELETE_FAILURE, MESSAGES_DELETE_SUCCESS } from "./actionTypes";
import nanoid from 'nanoid';
import { MESSAGE_STATUS_SENT, MESSAGE_STATUS_FAIL, MESSAGE_STATUS_PENDING } from "../constants";
import client from '../http'; // index.js –ø–∏—Å–∞—Ç—å –Ω–µ –Ω—É–∂–Ω–æ

export const messagesFetchRequest = () => {
    return {
        type: MESSAGES_FETCH_REQUEST,
        payload: {}
    }
}
export const messagesFetchSuccess = items => {
    return {
        type: MESSAGES_FETCH_SUCCESS,
        payload: { items }
    }
}
export const messagesFetchFailure = error => {
    return {
        type: MESSAGES_FETCH_FAILURE,
        payload: { error }
    }
}
// 1. –í–∞—Ä–∏–∞–Ω—Ç - –ø–æ–ø—Ä–æ—Å–∏—Ç—å lastSeenId –≤ –ø–∞—Ä–∞–º–µ—Ç—Ä—ã
// 2. –í–∞—Ä–∏–∞–Ω—Ç - –≤—ã—Ç–∞—Å–∫–∏–≤–∞—Ç—å –∏–∑ store
export const messagesFetch = () => async (dispatch, getState) => {
    const { lastSeenId } = getState().messages.list;
    dispatch(messagesFetchRequest())
    try {
        const { data } = await client.get('/messages', { params: { lastSeenId } }); // —Ç–µ–ø–µ—Ä—å .json() –∏–ª–∏ JSON.parse() –¥–µ–ª–∞—Ç—å –Ω–µ –Ω—É–∂–Ω–æ
        dispatch(messagesFetchSuccess(data)); // response - —Ü–µ–ª–∏–∫–æ–º –æ—Ç–≤–µ—Ç, data - –¥–∞–Ω–Ω—ã–µ —Ç–µ–ª–∞
    } catch (e) {
        dispatch(messagesFetchFailure(e));
    }
}

export const messageDelete = async (dispatch, id) => {
    dispatch(messagesDeleteRequest());
    try {
        await client.delete(`/messages/${id}`);
        dispatch(messagesDeleteSuccess(id));
    } catch (e) {
        dispatch(messagesDeleteFailure(e));
    }
}

