export const messagesDeleteRequest = () => {
    return {
        type: MESSAGES_DELETE_REQUEST,
        payload: {}
    }
}

export const messagesDeleteFailure = e => {
    return {
        type: MESSAGES_DELETE_FAILURE,
        payload: { e }
    }
}

export const messagesDeleteSuccess = id => {
    return {
        type: MESSAGES_DELETE_SUCCESS,
        payload: {
            id,
        }
    }
}

export const messagesSaveMessage = item => {
    return {
        type: MESSAGES_SAVE_MESSAGE,
        payload: { item },
    }
}

export const messagesMessageStatusChange = (id, status) => {
    return {
        type: MESSAGES_MESSAGE_STATUS_CHANGE,
        payload: { id, status },
    }
}

export const messageEdit = item => {
    return {
        type: MESSAGE_EDIT,
        payload: { item }
    };
}

export const messageChange = ({ content }) => {
    return {
        type: MESSAGE_CHANGE,
        payload: { content }
    };
}

export const messageSaveRequest = () => {
    return {
        type: MESSAGE_SAVE_REQUEST,
        payload: {}
    }
}

export const messageSaveSuccess = () => {
    return {
        type: MESSAGE_SAVE_SUCCESS,
        payload: {}
    }
}

export const messageSaveFailure = error => {
    return {
        type: MESSAGE_SAVE_FAILURE,
        payload: {
            error
        }
    }
}

