import React from 'react';
import { MESSAGE_STATUS_FAIL, MESSAGE_STATUS_SENT } from '../../constants';

// –ü–µ—Ä–µ–∏—Å–ø–æ–ª—å–∑—É–µ–º—ã–π –∫–æ–º–ø–æ–Ω–µ–Ω—Ç, –Ω–µ –¥–æ–ª–∂–µ–Ω –ª–∞–∑–∏—Ç—å –≤ state
export default function Message({ item, onEdit, onResend, onDelete }) {
    const handleEdit = () => {
        onEdit(item);
    };

    const handleResend = () => {
        onResend(item);
    };

    const handleDelete = () => {
        onDelete(item.id);
    }

    return (
        <div>
            {item.status === MESSAGE_STATUS_SENT && <button onClick={handleEdit}>‚úé</button>}
            {item.status === MESSAGE_STATUS_FAIL && <button onClick={handleResend}>üóò</button>}
            <button onClick={handleDelete}>delete</button>
            <p>
                {!item.removed && item.content}
            </p>
            <p>
                {!item.removed && item.id}
            </p>
            <p>
                {!item.removed && item.status}
            </p>
        </div>
    )
}
