import React from 'react'
import { useSelector, useDispatch } from 'react-redux';
import Message from '../Message/Message';
import { messageEdit, messageSave, messagesFetch, messageDelete } from '../../actions/actionCreators';
import { useEffect } from 'react';

export default function MessagesList() {
    const { items, loading } = useSelector(state => state.messages.list);
    const dispatch = useDispatch(); // react-redux - dispatch - –Ω–∏–∫–æ–≥–¥–∞ –Ω–µ –º–µ–Ω—è–µ—Ç—Å—è

    useEffect(() => {
        dispatch(messagesFetch()); // first load (–Ω–µ –∂–¥—ë–º —Å–µ–∫—É–Ω–¥—É)
    }, [dispatch]);

    useEffect(() => {
        // subscriptions (–ø–æ–¥–ø–∏—Å–∫–∏)
        // –ù–µ –∏—Å–ø–æ–ª—å–∑–æ–≤–∞—Ç—å setInterval - –µ—Å–ª–∏ –æ—Ç–≤–µ—Ç—ã –±—É–¥—É—Ç –º–µ–¥–ª–µ–Ω–Ω—ã–º–∏, —Ç–æ –º–æ–∂–Ω–æ –ø–µ—Ä–µ—Ç–µ—Ä–µ—Ç—å –¥–∞–Ω–Ω—ã–µ
        // const id = setInterval(() => {
        //     dispatch(messagesFetch());
        // }, 5000);
        // –≤—Å–µ–≥–¥–∞ –≤–æ–∑–≤—Ä–∞—â–∞–µ–º —Ñ—É–Ω–∫—Ü–∏—é unsubscribe (–æ—Ç–ø–∏—Å–∫–∞)
        // return () => clearInterval(id);
        let id = 0;
        if (loading === false) {
            id = setTimeout(() => {
                dispatch(messagesFetch());
            }, 1000);
            return () => clearTimeout(id);
        }
    }, [dispatch, loading]); // dispatch - deps (–≤—Å—ë, —á—Ç–æ –∏—Å–ø–æ–ª—å–∑—É–µ–º —Å–Ω–∞—Ä—É–∂–∏)

    const handleEdit = item => {
        dispatch(messageEdit(item));
    };

    const handleResend = item => {
        messageSave(dispatch, item);
    };

    const handleDelete = id => {
        messageDelete(dispatch, id);
    };

    return (
        <div>
            <ul>
                {items.map(o => <li key={o.id}>
                    <Message item={o} onEdit={handleEdit} onResend={handleResend} onDelete={handleDelete} />
                </li>)}
            </ul>
        </div>
    )
}
