import { combineReducers } from "redux";
import { MESSAGE_CHANGE, MESSAGE_SAVE_REQUEST, MESSAGES_SAVE_MESSAGE, MESSAGES_MESSAGE_STATUS_CHANGE, MESSAGE_EDIT, MESSAGES_FETCH_REQUEST, MESSAGES_FETCH_SUCCESS, MESSAGES_FETCH_FAILURE, MESSAGES_DELETE_REQUEST, MESSAGES_DELETE_SUCCESS } from "../actions/actionTypes";
import { MESSAGE_STATUS_PENDING } from "../constants";

const initialListState = {
    items: [],
    loading: false,
    error: null,
    lastSeenId: '', // –∫–∞–∫–æ–π –ø–æ—Å–ª–µ–¥–Ω–∏–π id –º—ã –≤–∏–¥–µ–ª–∏
};

// state <- list
export function messagesListReducer(state = initialListState, action) {
    // TODO: handle action
    switch (action.type) {
        // action –ø—Ä–æ—Ö–æ–¥–∏—Ç —á–µ—Ä–µ–∑ –≤—Å–µ reducer'—ã (–º–æ–∂–Ω–æ —Å–¥–µ–ª–∞—Ç—å –∏ —Ç–∞–∫, –Ω–æ —ç—Ç–æ —á–∞—Å—Ç–æ –ø—Ä–∏–≤–æ–¥–∏—Ç –∫ –æ—à–∏–±–∫–∞–º)
        // case MESSAGE_SAVE_SUCCESS: {
        //     console.log('received');
        // }
        case MESSAGES_FETCH_REQUEST: {
            return { ...state, loading: true, error: null };
        }
        case MESSAGES_FETCH_SUCCESS: {
            const { items } = action.payload;
            const ids = items.map(o => o.id);
            let last;
            if (items.length !== 0) {
                last = items[items.length - 1];
            }
            const lastSeenId = ((last && last.id) || state.lastSeenId); // —Ç–æ–ª—å–∫–æ –¥–ª—è —Å–æ–±–µ—Å–µ–¥–æ–≤–∞–Ω–∏—è
            // 1. –ù–µ –¥–æ–±–∞–≤–ª—è—Ç—å —Ç–µ —ç–ª–µ–º–µ–Ω—Ç—ã, –∫–æ—Ç–æ—Ä—ã–µ —É–∂–µ –µ—Å—Ç—å
            // 2. –°–¥–µ–ª–∞—Ç—å —É–¥–∞–ª–µ–Ω–∏–µ (–ø—É—Å—Ç—å –∞–≤—Ç–æ–º–∞—Ç–∏—á–µ—Å–∫–∏ —É –¥—Ä—É–≥–∏—Ö –Ω–µ —É–¥–∞–ª—è–µ—Ç—Å—è) - —É–¥–∞–ª—è–µ—Ç—Å—è —Ç–æ–ª—å–∫–æ —É –≤–∞—Å
            console.log(items);
            return { ...state, items: [...state.items.filter(o => !ids.includes(o.id)), ...items], loading: false, error: null, lastSeenId }; // TODO: –ó–∞–º–µ–Ω—è—Ç—å –≤—Å—ë –ø–ª–æ—Ö–æ
        }
        case MESSAGES_FETCH_FAILURE: {
            const { error } = action.payload;
            return { ...state, loading: false, error };
        }
        case MESSAGES_SAVE_MESSAGE: {
            const { item } = action.payload;
            // TODO: handle edit of existing item
            const existing = state.items.find(o => o.id === item.id);
            if (existing === undefined) {
                return { ...state, items: [...state.items, item] };
            }
            return { ...state, items: state.items.map(o => o.id === item.id ? item : o) };
        }
        case MESSAGES_MESSAGE_STATUS_CHANGE: {
            const { id, status } = action.payload;
            return { ...state, items: state.items.map(o => o.id === id ? { ...o, status } : o) };
        }
        case MESSAGES_DELETE_REQUEST: {
            return { ...state, loading: true, error: null };
        }
        case MESSAGES_DELETE_SUCCESS:
            return { ...state, items: state.items.filter(o => o.id !== action.payload.id) }
        default:
            return state;
    }

}

const initialEditState = {
    item: {
        id: '',
        content: '',
        // type <- TODO: file uploading
        status: MESSAGE_STATUS_PENDING,
        created: 0,
        removed: false,
    },
    loading: false,
    error: null,
}

// 1. Status Component -> —á–∞—Å–∏–∫–∏, –≥–∞–ª–æ—á–∫–∞, –≤–æ—Å–∫–ª.–∑–Ω–∞–∫
// 2. –ï—Å–ª–∏ –≤–æ—Å–∫–ª.–∑–Ω–∞–∫ - —Ç–æ–≥–¥–∞ –¥–æ–ª–∂–Ω–∞ –ø–æ—è–≤–∏—Ç—å—Å—è –∫–Ω–æ–ø–∫–∞ Resend
// 3. –ò–∑ —Ñ–æ—Ä–º—ã –≤—ã—Ä–µ–∑–∞—Ç—å –≤—Å—ë –Ω–µ–Ω—É–∂–Ω–æ–µ
// 4. –î–æ–±–∞–≤–∏—Ç—å –∫–Ω–æ–ø–∫—É —Ä–µ–¥–∞–∫—Ç–∏—Ä–æ–≤–∞–Ω–∏—è
// 5. –û—Ñ–æ—Ä–º–∏—Ç—å —Å–æ–æ–±—â–µ–Ω–∏—è

// state <- edit
export function messageEditReducer(state = initialEditState, action) {
    switch (action.type) {
        case MESSAGE_EDIT: {
            const { item } = action.payload;
            return { ...state, item };
        }
        case MESSAGE_CHANGE: {
            const { content } = action.payload;
            return { ...state, item: { ...state.item, content } };
        }
        case MESSAGE_SAVE_REQUEST: {
            // —Å—Ä–∞–∑—É –≤—ã—á–∏—â–∞–µ–º –ø–æ–ª–µ –≤–≤–æ–¥–∞ –∫–∞–∫ –≤ Telegram
            return { item: { ...initialEditState.item }, loading: true, error: null };
        }
        default:
            return state;
    }
}



/*
messages: {
    list: { <- messagesListReducer
        items: [],
        loading: false,
        error: null,
    },
    edit: { <- messageEditReducer
        item: {...},
        loading: false,
        error: null,
    }
}
*/

const messagesReducer = combineReducers({
    list: messagesListReducer,
    edit: messageEditReducer,
});

export default messagesReducer;